
$bp-400px: 25em; //  400px
$bp-500px: 31.25em; //  500px
$bp-600px: 37.5em;      //  600px
$bp-800px: 50em;   // 800px
$bp-1000px: 62.5em;        // 1000px
$bp-1200px: 75em;        // 1200px



* {
    margin: 0;
    padding: 0;
}

*,
*::before,
*::after {
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
}

html {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size: 65.5%; // 1rem = 10px
    height: 100vh;
    width: 100vw;
    position: relative;
    background: black;
    }

body {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
    font-size: 65.5%; // 1rem = 10px
    height: 100vh;
    width: 100vw;

}


.preloader {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background-image: url('/loader.gif');
    background-repeat: no-repeat; 
    background-color: rgb(0, 0, 0);
    background-position: center;
    opacity: 1;
 }

.fader-page {
    position: absolute;
    height: 100%;
    width: 100%;
    opacity: 0;
    background: yellow;
    
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}


