section {
    height: 100vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: space-evenly;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.title-scroll {
    position: absolute;
    height: 10vh;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    opacity: 0;
    

    &:hover a {
        color: rgb(10, 167, 229);
    }

    &-up {
        top: 0;
    }

    &-down {
        bottom: 0;
    }

    
    & i {
            border: solid #56E195;
            border-width: 0 3px 3px 0;
            display: inline-block;
            padding: 3px;
    }

    & .up {
        transform: rotate(-135deg);
        -webkit-transform: rotate(-135deg);

        -webkit-animation: moveUp 2s infinite alternate;

                animation: moveUp 2s infinite alternate;
      }

    & .down {
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        -webkit-animation: moveDown 2s infinite alternate;
                animation: moveDown 2s infinite alternate;
      }

    & h2 {
        
        font-size: 1.6rem;
        font-family: 'Open Sans', sans-serif;
        font-weight: 400;
        letter-spacing: 2px;

        & a {
            text-decoration: none;
            color: white;
            -webkit-transition: color 0.2s ease-in;
            transition: color 0.2s ease-in;
        }
    }
    
    
}






.section-name {
    font-family: 'Montserrat' ,sans-serif;
    font-weight: 800;
    text-transform: lowercase;
    color: rgb(20, 34, 52);
    font-size: 8rem;
    -ms-flex-item-align: end;
        align-self: flex-end;
    text-shadow: 2px 2px 3px rgba(0,0,0,0.3);
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
    opacity: 0;
    position: absolute;
    
    right: 5px;
    bottom: 15%;

    &.name-2 {
        opacity: 0;
    }
    &.name-3 {
        opacity: 0;
    }

    
    @media only screen and (min-width: $bp-400px) {
        font-size: 10rem;
    }

    @media only screen and (min-width: $bp-600px) {
        font-size: 14rem;
    }

    @media only screen and (min-width: $bp-1000px) {
        font-size: 20rem;
    }
}



